a {
  text-decoration: none;
}

.main-block {
  width: 75%;
}

.flexBusca {
  box-sizing: border-box;
  display: flex;
}

.caixaBusca {
  background-image: url('../images/lupa.svg');
  background-position: 5px;
  background-repeat: no-repeat;
  background-size: contain;
  border: 2px solid black;
  border-radius: 20px;
  box-sizing: border-box;
  flex-shrink: 1;
  font-size: 20px;
  height: 40px;
  padding-left: 50px;
  width: 90%;
}

.carrinhoBusca {
  height: 40px;
  margin-left: 2%;
  width: 40px;
}

.imgCarrinho {
  height: 40px;
}

.valorCarrinho {
  bottom: 38px;
  display: inline-block;
  font-weight: 700;
  left: 7px;
  position: relative;
  text-align: center;
  width: 30px;
}

a .valorCarrinho {
  color: black;
}

a:hover .valorCarrinho {
  color: green;
}

.buscaZerada {
  font-size: 36px;
  font-weight: 700;
  margin-top: 50px;
  text-align: center;
  width: 95%;
}
